<!--商品上架管理-->
<template>
  <div>
    <el-form class="account-plat-formitem" label-position="left" :model="ruleForm" ref="ruleForm" label-width="130px"
             style="text-align: left;font-size:20px;font-weight:
      bold;" @submit.native.prevent>
      <el-card class="account-plat-card1 account-plat-card2" shadow="always" :body-style="cardBodyStyle" style="">
        <div style="margin-top: 10px">
          <el-row :gutter="24">
            <el-col :md="24">
              <el-form-item label="选择设备:" prop="di_uuid">
                <el-select placeholder="请选择设备" v-model="ruleForm.di_uuid" style="width: 300px"
                           @change="equipment_Selection"
                >
                  <el-option
                    v-for="item in tableData_equipment"
                    :key="item.di_uuid"
                    :label="item.Id"
                    :value="item.di_uuid"
                  >
                  </el-option>
                </el-select>
              </el-form-item>
            </el-col>
          </el-row>
        </div>
      </el-card>
    </el-form>
    <el-menu :default-active="activeIndex" :style="{ height: '60px'}" mode="horizontal" @select="handleSelect">
      <el-menu-item index="1" style="margin: 0 10px">
        <span>设备外商品</span>
      </el-menu-item> 
      <el-menu-item style="margin: 0 10px ">
        <div style="width: 50px;height: 50px;" @click="dialogVisible12 = true">
          <img style="width: 50px;" src="../../../../assets/images/book.png" alt="">
        </div>
      </el-menu-item>
      <el-menu-item v-show="false" index="2" style="margin: 0 10px">
        <span>设备内商品</span>
      </el-menu-item>
    </el-menu>
    <el-dialog title="提示" :visible.sync="dialogVisible12" width="50%" >
      <div style="display: flex;justify-content: center;">
        <iframe width="700px" height="450px" seamless='seamless' frameborder='0' ref="iframe" :src="customGlobal.serverUrl.orgBaseURI+'/fr/imgserver/admin/ydzfalmb.pdf#scrollbar=0&toolbar=0&statusbar=0'"></iframe>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button type="primary" @click="dialogVisible12 = false">确 定</el-button>
      </span>
    </el-dialog>
    <!--  页面表格 start -->
    <el-card>
      <div v-if="this.tableData_equipment.length === 0" style="color: red;margin: 20px 0">请先创建设备</div>
      <div class="box" style="margin: 0 20px 10px 0" v-if="this.tableData_equipment.length !== 0">
        <el-button style="float:right;" type="primary" @click="add_commodity"><i
          class="el-icon-edit">添加商品</i></el-button>
      </div>
      <el-table
        class="account-plat-fixed-right account-plat-fixed "
        border
        ref="tableData"
        :data="tableData"
        tooltip-effect="dark"
        @sort-change="sortColumn"
        :row-style="{'height':'10px'}"
        :cell-style="{'padding':'5px 0px'}"
        :header-cell-style="{'background': '#4B86BC','color':'#ffffff','borderRight': '1px solid #E3E5EB' ,'padding':'0px','height':'45px','line-height':'45px' }"
      >
        <!--        <el-table-column
                  type="selection"
                  :selectable="isSelect"
                  width="55">
                </el-table-column>-->
        <el-table-column
          fixed
          align="center"
          type="index"
          label="序号"
          width="50">
        </el-table-column>
        <el-table-column
          prop="mbgi_goods_name"
          label="商品名称"
          align="center"
          show-overflow-tooltip
          width="150"
        >
        </el-table-column>
        <el-table-column
          prop="mbgi_goods_img_path"
          label="商品图片"
          align="center"
          show-overflow-tooltip
          width="150"
        >
          <template slot-scope="scope">
            <el-image style="width: 80px;height: 60px;" :src="scope.row.mbgi_goods_img_path"/>
          </template>
        </el-table-column>
        <el-table-column
          prop="mbgi_goods_describer"
          label="规格"
          align="center"
          show-overflow-tooltip
          width="100"
        >
        </el-table-column>
        <el-table-column
          prop="mbmi_mer_name"
          label="商品归属"
          align="center"
          show-overflow-tooltip
          width="300"
        >
        </el-table-column>
        <el-table-column
          prop="mbgi_money_1"
          label="原价"
          align="center"
          show-overflow-tooltip
          width="100"
        >
        </el-table-column>
        <el-table-column
          prop="mbgi_money_2"
          label="优惠价"
          align="center"
          show-overflow-tooltip
          width="100"
        >
        </el-table-column>
        <el-table-column
          prop="mbgi_money_3"
          label="特惠价"
          align="center"
          show-overflow-tooltip
          width="100"
        >
        </el-table-column>
        <el-table-column
          prop="count_num"
          label="库存"
          align="center"
          show-overflow-tooltip
          width="100"
        >
        </el-table-column>
        <el-table-column
          fixed="right"
          label="操作"
          min-width="300"
          align="center"
        >
          <template slot-scope="scope" v-if="scope.row.lai_detail_status !=='4'">
          <el-button @click.native.stop="update_commodity(scope.$index,scope.row)" type="text" size="small">编辑</el-button>
          <el-button @click.native.stop="off_the_shelf(scope.$index,scope.row)" type="text" size="small">下架</el-button>
            <!-- <el-tooltip content="编辑" placement="top">
              <el-button
                type="primary" icon="el-icon-edit-outline" circle
                size="mini"
                @click.native.stop="update_commodity(scope.$index,scope.row)"></el-button>
            </el-tooltip>
            <el-tooltip content="下架" placement="top">
              <el-button
                type="danger" icon="el-icon-document-delete" circle
                size="mini"
                @click.native.stop="off_the_shelf(scope.$index,scope.row)"></el-button>
            </el-tooltip> -->
          </template>
        </el-table-column>
      </el-table>
      <!--  页面表格 end   -->
      <!--page start-->
      <el-pagination
        style="margin-top: 20px"
        background
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryParam.currentPage"
        :page-sizes="[10, 20, 50, 100,200]"
        :page-size="queryParam.pageSize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="queryParam.totalRow">
      </el-pagination>
      <!--page end-->
    </el-card>
    <!--页面 end-->
    <el-dialog
      :show-close="false"
      :close-on-press-escape="false"
      :close-on-click-modal="false"
      :visible.sync="dialogVisible"
      width="60%"
      class="account-plat-dialog"
    >
      <div slot="title" style="text-align: left;font-size:20px;font-weight: bold;">
        <span>{{ this.tableDialgTitle }}</span>
      </div>
      <el-form class="account-plat-formitem" :model="commodityForm" ref="commodityForm" label-width="160px" style="position: relative"
               :rules="commodityFormrules">
        <el-row :gutter="24">
          <el-col :md="12">
            <el-form-item label="商品所属商户:" prop="bai_num_count">
              <el-select placeholder="请选择商户" v-model="commodityForm.mbmi_mer_name" style="width: 300px"
                         :disabled="disabled2"
                         @change="shop_but"
              >
                <el-option
                  v-for="item in tableData_shop"
                  :key="item.mbmi_uuid"
                  :label="item.mbmi_mer_name"
                  :value="item.mbmi_uuid"
                >
                </el-option>
              </el-select>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row :gutter="24">
          <el-col :md="12">
            <el-form-item label="请选择商品:" prop="mbgi_goods_name">
              <el-select placeholder="请选择商品" v-model="commodityForm.mbgi_goods_name" style="width: 300px"
                         :disabled="disabled3"
                         @change="commodity_but"
              >
                <el-option
                  v-for="item in tableData_commodity"
                  :key="item.mbgi_uuid"
                  :label="item.mbgi_goods_name"
                  :value="item"
                >
                </el-option>
              </el-select>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row :gutter="24">
          <el-col :md="12">
            <el-form-item label="原价:" prop="mbgi_money_1" label-width="130px">
              <el-input placeholder="请输入价格" v-model="commodityForm.mbgi_money_1" :disabled="disabled1"
                        style="width: 250px"></el-input>
              元
            </el-form-item>
          </el-col>
        </el-row>
        <el-row :gutter="24">
          <el-col :md="12">
            <el-form-item label="优惠价:" prop="mbgi_money_2" label-width="130px" class="Tips">
              <el-input placeholder="请输入价格" :maxlength="5" v-model="commodityForm.mbgi_money_2" :disabled="disabled"
                        :rules="commodityFormrules.mbgi_money_2"
                        style="width: 250px"></el-input>
              元
            </el-form-item>
          </el-col>
        </el-row>
        <el-row :gutter="24">
          <el-col :md="12">
            <el-form-item label="特惠价:" prop="mbgi_money_3" label-width="130px" class="Tips">
              <el-input placeholder="请输入价格" :maxlength="5" v-model="commodityForm.mbgi_money_3" :disabled="disabled"
                        :rules="commodityFormrules.mbgi_money_3"
                        style="width: 250px"></el-input>
              元
            </el-form-item>
          </el-col>
        </el-row>
        <el-row :gutter="24" v-show="false">
          <el-col :md="12">
            <el-form-item label="原库存数量:" prop="inventory_quantity" label-width="130px">
              <el-input placeholder="请输入内容,最多支持6个字" :maxlength="2" v-model="inventory_quantity" :disabled="disabled1"
                        style="width: 250px"></el-input>
              个
            </el-form-item>
          </el-col>
        </el-row>
        <el-row :gutter="24">
          <el-col :md="12">
            <el-form-item label="库存数量:" prop="mbgi_count_num" label-width="130px" class="Tips">
              <el-input placeholder="请输入库存数量" :maxlength="2" v-model.number="commodityForm.mbgi_count_num" :disabled="disabled"
                        :rules="commodityFormrules.mbgi_money_3"
                        style="width: 250px"></el-input>
              个
            </el-form-item>
          </el-col>
        </el-row>
        <div style="position: absolute;top: 100px;right: 13%;">
          <el-image style="width: 200px;height: 150px;" :src="mbgi_goods_img_path"/>
          <div style="text-align: center;margin-top: 20px">商品图片</div>
        </div>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="cancelForm('commodityForm')">取 消</el-button>
        <el-button type="primary" @click="submitForm('commodityForm')">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: "CommodityShelves",
  data() {
    let mbgi_money_2 = (rule, value, callback) => {
      let patrn = /^(\d+)(\.\d+)?$/;
      if (value.length === 0) {
        callback(new Error('优惠价不能为空'));
      } else {
        if (patrn.test(value)) {
          console.info("true" + ":" + value);
          callback();
        } else {
          callback(new Error('优惠价不能小于0'));
        }
      }
    };
    let mbgi_money_3 = (rule, value, callback) => {
      // let patrn1 = /^[1-9][0-9]{0,4}$|^[1-9][0-9]{0,2}\.[1-9]$|^0\.[1-9]$|^0$/
      // let patrn2 = /^[0-9]+(.[0-9]{1,2})?$/
      //   if (!patrn1.test(value)) {
      //     callback(new Error('特惠价只能为数字1'));
      //     }else if(!patrn2.test(value)) {
      //     callback(new Error('特惠价只能为数字2'));
      //     }else {
      //     callback();
      //   }

      let patrn = /^(\d+)(\.\d+)?$/;
      if (value.length === 0) {
        callback(new Error('特惠价不能为空'));
      } else {
        if (patrn.test(value)) {
          console.info("true" + ":" + value);
          callback();
        } else {
          callback(new Error('特惠价不能小于0'));
        }
      }
    };
    let mbgi_count_num = (rule, value, callback) => {
      let patrn = /^(\d+)(\.\d+)?$/;
      if (value.length === 0) {
        callback(new Error('库存数量不能为空'));
      } else {
        if (patrn.test(value)) {
          console.info("true" + ":" + value);
          callback();
        } else {
          callback(new Error('库存数量不能小于0'));
        }
      }
    };
    return {
      activeIndex: '1',
      cardBodyStyle: 'padding-top:10px;padding-bottom:10px;',
      ruleForm: {
        di_uuid: '',
      },
      tableData: [],//表格数据
      sortOrderCol: '',
      key: '',
      dialogVisible: false,//新增，修改商品弹窗开关
      dialogVisible12: false,//新增，修改商品弹窗开关
      tableDialgTitle: '',//弹窗头部文字
      commodityForm: {
        mbmi_mer_name: '',
        mbgi_money_1: '',
        mbgi_money_2: '',
        mbgi_money_3: '',
        mbgi_count_num: '',
        mbgi_goods_name: ''
      },
      commodityFormrules: {
        mbgi_money_2: [
          {required: true, validator: mbgi_money_2, trigger: 'blur'}
        ],
        mbgi_money_3: [
          {required: true, validator: mbgi_money_3, trigger: 'blur'}
        ],
        mbgi_count_num: [
          {required: true, validator: mbgi_count_num, trigger: 'blur'}
        ],
      },
      tableData_shop: [],
      tableData_commodity: [],
      mbgi_goods_img_path: '',
      but: '',//区分新增还是修改
      disabled: false,//所有的开关
      disabled1: false,//原价开关
      disabled2: false,//商户开关
      disabled3: false,//商品开关
      shop_id: '',//商户id
      mbgi_uuid: '',//商品id
      inventory_quantity: '',//库存数量
      tableData_equipment: [],//设备数据
      di_uuid: '',//设备id
      /** 查询参数 */
      queryParam: {
        totalRow: 0,
        currentPage: 1,
        pageSize: this.customGlobal.pageSize,
      },
    }
  },
  created() {
    this.select_equipment();
  },
  methods: {
    /**----分页方法-------start----*/
    handleSizeChange(val) {
      //  console.log(`每页 ${val} 条`);
      this.queryParam.pageSize = val;
      this.select_flag_equipment();
    },
    handleCurrentChange(val) {
      //  console.log(`当前页: ${val}`);
      this.queryParam.currentPage = val;
      this.select_flag_equipment();
    },
    handleSelect(key, keyPath) {
      console.log('上：' + key, keyPath);
      this.key = key;
      // if(this.key === '1'){
      //   this.mbmi_status = '2';
      // }else if (this.key === '2'){
      //   this.mbmi_status = '1';
      // }
      // this.queryParam.currentPage = 1;
      // this.select_table();
    },
    /**  表格列排序设置   */
    sortColumn(column) {
      //  console.log("已经排序项：" + JSON.stringify(column));
      let orderFlag = '';
      if (column.order != null) {
        if (column.order === "descending") {
          orderFlag = 'desc';
        } else if (column.order === "ascending") {
          orderFlag = 'asc';
        }
        //  console.info("属性名称："+ column.prop+",排序方式："+column.order);
        this.sortOrderCol = column.prop + " " + orderFlag;
        //  console.info("用户选择排序列："+this.sortOrderCol);
        this.select_flag_equipment();
      } else {
        this.sortOrderCol = '';
      }

    },
    //下拉框点击事件
    equipment_Selection(key) {
      this.di_uuid = key;
      this.ruleForm.di_uuid = key;
      this.queryParam.currentPage = 1;
      console.log('res==', this.di_uuid,'---------------------------------------------------------');
      this.select_flag_equipment();
    },
    //添加商品
    add_commodity() {
      this.tableDialgTitle = '添加商品';
      this.dialogVisible = true;
      this.commodityForm.mbmi_mer_name = '';
      this.commodityForm.mbgi_money_1 = '';
      this.commodityForm.mbgi_money_2 = '';
      this.commodityForm.mbgi_money_3 = '';
      this.commodityForm.mbgi_count_num = '';
      this.commodityForm.mbgi_goods_name = '';
      this.mbgi_goods_img_path = '';
      this.select_shop();
      this.but = '1';
      this.disabled = true;
      this.disabled1 = true;
      this.disabled2 = false;
      this.disabled3 = true;
    },
    //取消
    cancelForm() {
      this.dialogVisible = false;
    },
    //提交
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.submitForm_but();
        } else {
          return false;
        }
      });
    },
    shop_but(key) {
      console.log("key:" + key);
      this.shop_id = key;
      // this.disabled = true;
      // this.disabled1 = true;
      this.disabled3 = false;
      this.select_commodity();
    },
    commodity_but(e) {
      this.disabled1 = true;
      this.disabled = false;
      this.commodityForm.mbgi_money_1 = e.mbgi_money_1;
      this.mbgi_goods_img_path = e.mbgi_goods_img_path;
      this.mbgi_uuid = e.mbgi_uuid;
      this.inventory_quantity = e.inventory_quantity;
      this.commodityForm.mbgi_count_num = this.inventory_quantity;
      this.commodityForm.mbgi_goods_name = e.mbgi_goods_name;
      console.log("选中的集合:" + JSON.stringify(e));
      console.log("原价:" + JSON.stringify(this.commodityForm.mbgi_money_1));
      console.log("图片:" + JSON.stringify(this.mbgi_goods_img_path));
      console.log("商品id:" + JSON.stringify(this.mbgi_uuid));
    },
    //查询商户
    select_shop() {
      let that = this;
      that.tableData_shop = [];
      let _jsondata_ = {
        obj_bank_center_id: that.customGlobal.userInfoExt.cui_bank_center_id,
        obj_province_id: that.customGlobal.userInfoExt.cui_province_id,
        obj_city_id: that.customGlobal.userInfoExt.cui_city_id,
        obj_county_id: that.customGlobal.userInfoExt.cui_county_id,
        obj_station_id: that.customGlobal.userInfoExt.cui_station_id,
        perPageSize: 1000,
        currentPage: 1,
      };
      const loading = this.$loading({
        lock: true,
        text: '提交中...',
        target: '.main',
        spinner: 'el-icon-loading',

        background: 'rgba(0, 0, 0, 0)'
      });
      var a1 = this.customGlobal.postHttpClient(
        this.customGlobal.serverUrl.Commodity_merchant_select,
        //this.customGlobal.serverUrl.baseURI,
        this.customGlobal.serverUrl.orgBaseURI,
        {"Content-Type": "application/x-www-form-urlencoded"},
        this.qs.stringify({jsondata: JSON.stringify(_jsondata_)})
      );
      a1.then((res) => {
        if (res.data.status === 'success') {
          loading.close();
          that.tableData_shop = res.data.rows;
        } else {
          console.info("商户查询失败");
          loading.close();
          if (res.data.msg === '请登录认证') {
            this.$router.push({path: '/login'});
          }
        }
      }, error => {
        loading.close();
        // this.companyDialogFormVisible =false;//保存失败关闭dialog
        this.$alert(JSON.stringify(error.message), '商户查询失败!', {
          confirmButtonText: '确定',
          callback: action => {

          }
        });
      }).catch((res) => {
        console.log("异步调用失败返回:" + JSON.stringify(res));
        loading.close();
        // this.companyDialogFormVisible =false;//保存失败关闭dialog
        this.$alert(JSON.stringify(res.message), '商户查询失败', {
          confirmButtonText: '确定',
          callback: action => {

          }
        });
      });
    },
    //查询商品
    select_commodity() {
      let that = this;
      that.tableData_commodity = [];
      let _jsondata_ = {
        obj_bank_center_id: that.customGlobal.userInfoExt.cui_bank_center_id,
        obj_province_id: that.customGlobal.userInfoExt.cui_province_id,
        obj_city_id: that.customGlobal.userInfoExt.cui_city_id,
        obj_county_id: that.customGlobal.userInfoExt.cui_county_id,
        obj_station_id: that.customGlobal.userInfoExt.cui_station_id,
        mbgi_mer_uuid: that.shop_id,
        perPageSize: 1000,
        currentPage: 1,
      };
      const loading = this.$loading({
        lock: true,
        text: '提交中...',
        target: '.main',
        spinner: 'el-icon-loading',

        background: 'rgba(0, 0, 0, 0)'
      });
      var a1 = this.customGlobal.postHttpClient(
        this.customGlobal.serverUrl.Merchandise_under_merchant_select,
        //this.customGlobal.serverUrl.baseURI,
        this.customGlobal.serverUrl.orgBaseURI,
        {"Content-Type": "application/x-www-form-urlencoded"},
        this.qs.stringify({jsondata: JSON.stringify(_jsondata_)})
      );
      a1.then((res) => {
        if (res.data.status === 'success') {
          loading.close();
          that.tableData_commodity = res.data.rows;
        } else {
          console.info("商品查询失败");
          loading.close();
          if (res.data.msg === '请登录认证') {
            this.$router.push({path: '/login'});
          }
        }
      }, error => {
        loading.close();
        // this.companyDialogFormVisible =false;//保存失败关闭dialog
        this.$alert(JSON.stringify(error.message), '商品查询失败!', {
          confirmButtonText: '确定',
          callback: action => {

          }
        });
      }).catch((res) => {
        console.log("异步调用失败返回:" + JSON.stringify(res));
        loading.close();
        // this.companyDialogFormVisible =false;//保存失败关闭dialog
        this.$alert(JSON.stringify(res.message), '商品查询失败', {
          confirmButtonText: '确定',
          callback: action => {

          }
        });
      });
    },
    //提交接口
    submitForm_but() {
      let that = this;
      let num = that.commodityForm.mbgi_count_num - that.inventory_quantity;
      console.log("库存数量：" + num)
      let patrn = /^(\d+)(\.\d+)?$/;
      let type = '';
      if (patrn.test(num)) {
        console.info("true" + ":" + num);
        if (num == '0') {
          type = '3';
        } else {
          type = '1';
        }
      } else {
        console.info("false" + ":" + num);
        num = Math.abs(num);
        type = '2';
      }
      let _jsondata_ = {
        flag: '1',
        type: type,
        mgs_mer_uuid: that.shop_id,
        mgs_goods_type_uuid: that.mbgi_uuid,
        mbgi_money_2: that.commodityForm.mbgi_money_2,
        mbgi_money_3: that.commodityForm.mbgi_money_3,
        count_num: num,
        mgs_device_uuid: that.di_uuid,
        mgs_bank_center_id: that.customGlobal.userInfoExt.cui_bank_center_id,
        mgs_province_id: that.customGlobal.userInfoExt.cui_province_id,
        mgs_city_id: that.customGlobal.userInfoExt.cui_city_id,
        mgs_county_id: that.customGlobal.userInfoExt.cui_county_id,
        mgs_station_id: that.customGlobal.userInfoExt.cui_station_id,
      };
      const loading = this.$loading({
        lock: true,
        text: '提交中...',
        target: '.main',
        spinner: 'el-icon-loading',

        background: 'rgba(0, 0, 0, 0)'
      });
      var a1 = this.customGlobal.postHttpClient(
        this.customGlobal.serverUrl.postCUDMobileBankGoodsShelvesInfo,
        //this.customGlobal.serverUrl.baseURI,
        this.customGlobal.serverUrl.orgBaseURI,
        {"Content-Type": "application/x-www-form-urlencoded"},
        this.qs.stringify({jsondata: JSON.stringify(_jsondata_)})
      );
      a1.then((res) => {
        if (res.data.status == 'success') {
          loading.close();
          this.$message({
            type: 'success',
            showClose: true,
            duration: 3000,
            dangerouslyUseHTMLString: true,
            message: '<span style="color:black;">添加商品成功</span>'
          });
          that.dialogVisible = false;
          this.queryParam.currentPage = 1;
          that.select_flag_equipment();
        } else {
          console.info("添加商品失败");
          loading.close();
          if (res.data.msg === '请登录认证') {
            this.$router.push({path: '/login'});
          }
        }
      }, error => {
        loading.close();
        // this.companyDialogFormVisible =false;//保存失败关闭dialog
        this.$alert(JSON.stringify(error.message), '添加商品出错!', {
          confirmButtonText: '确定',
          callback: action => {

          }
        });
      }).catch((res) => {
        console.log("异步调用失败返回:" + JSON.stringify(res));
        loading.close();
        // this.companyDialogFormVisible =false;//保存失败关闭dialog
        this.$alert(JSON.stringify(res.message), '添加商品出错', {
          confirmButtonText: '确定',
          callback: action => {

          }
        });
      });
    },
    //查询设备
    select_equipment() {
      let that = this;
      that.tableData_equipment = [];
      let _jsondata_ = {
        obj_bank_center_id: that.customGlobal.userInfoExt.cui_bank_center_id,
        obj_province_id: that.customGlobal.userInfoExt.cui_province_id,
        obj_city_id: that.customGlobal.userInfoExt.cui_city_id,
        obj_county_id: that.customGlobal.userInfoExt.cui_county_id,
        obj_station_id: that.customGlobal.userInfoExt.cui_station_id,
        perPageSize: 1000,
        currentPage: 1,
      };
      const loading = this.$loading({
        lock: true,
        text: '提交中...',
        target: '.main',
        spinner: 'el-icon-loading',

        background: 'rgba(0, 0, 0, 0)'
      });
      var a1 = this.customGlobal.postHttpClient(
        this.customGlobal.serverUrl.Device_unique_ID_select,
        //this.customGlobal.serverUrl.baseURI,
        this.customGlobal.serverUrl.orgBaseURI,
        {"Content-Type": "application/x-www-form-urlencoded"},
        this.qs.stringify({jsondata: JSON.stringify(_jsondata_)})
      );
      a1.then((res) => {
        if (res.data.status === 'success') {
          loading.close();
          that.tableData_equipment = res.data.rows;
          that.tableData_equipment.forEach(val => {
            val.Id = val.di_uuid.substring(val.di_uuid.length-9,val.di_uuid.length)
          });
          console.log('res==', that.tableData_equipment,'---------------------------------------------------------');
          if (res.data.rows.length !== 0){
            that.ruleForm.di_uuid = that.tableData_equipment[0].di_uuid;
            that.di_uuid = that.ruleForm.di_uuid;
            this.queryParam.currentPage = 1;
            that.select_flag_equipment();
          }
        } else {
          console.info("设备id查询失败");
          loading.close();
          if (res.data.msg === '请登录认证') {
            this.$router.push({path: '/login'});
          }
        }
      }, error => {
        loading.close();
        // this.companyDialogFormVisible =false;//保存失败关闭dialog
        this.$alert(JSON.stringify(error.message), '设备id查询失败!', {
          confirmButtonText: '确定',
          callback: action => {

          }
        });
      }).catch((res) => {
        console.log("异步调用失败返回:" + JSON.stringify(res));
        loading.close();
        // this.companyDialogFormVisible =false;//保存失败关闭dialog
        this.$alert(JSON.stringify(res.message), '设备id查询失败', {
          confirmButtonText: '确定',
          callback: action => {

          }
        });
      });
    },
    //修改
    update_commodity(index, row) {
      this.dialogVisible = true;
      this.commodityForm.mbmi_mer_name = row.mbmi_mer_name;
      this.commodityForm.mbgi_goods_name = row.mbgi_goods_name;
      this.commodityForm.mbgi_money_1 = row.mbgi_money_1;
      this.commodityForm.mbgi_money_2 = row.mbgi_money_2;
      this.commodityForm.mbgi_money_3 = row.mbgi_money_3;
      this.commodityForm.mbgi_count_num = row.count_num;
      this.mbgi_goods_img_path = row.mbgi_goods_img_path;
      this.disabled = false;
      this.disabled1 = true;
      this.disabled2 = true;
      this.disabled3 = true;
      this.but = '2';
      this.inventory_quantity = row.count_num;
      this.shop_id = row.mbgi_mer_uuid;
      this.mbgi_uuid = row.mbgd_goods_type_uuid;
      this.di_uuid = row.mbgd_device_uuid;
      console.log(this.shop_id)
    },
    //下架
    off_the_shelf(index, row) {
      this.$confirm('确定要下架商品么?', '下架提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning',
        dangerouslyUseHTMLString: true,
        closeOnClickModal: false,
        closeOnPressEscape: false,
      }).then(() => {
        let that = this;
        let _jsondata_ = {
          flag: '2',
          mgs_goods_type_uuid: row.mbgd_goods_type_uuid,
          mgs_device_uuid: row.mbgd_device_uuid,
        };
        const loading = this.$loading({
          lock: true,
          text: '提交中...',
          target: '.main',
          spinner: 'el-icon-loading',

          background: 'rgba(0, 0, 0, 0)'
        });
        var a1 = this.customGlobal.postHttpClient(
          this.customGlobal.serverUrl.postCUDMobileBankGoodsShelvesInfo,
          //this.customGlobal.serverUrl.baseURI,
          this.customGlobal.serverUrl.orgBaseURI,
          {"Content-Type": "application/x-www-form-urlencoded"},
          this.qs.stringify({jsondata: JSON.stringify(_jsondata_)})
        );
        a1.then((res) => {
          if (res.data.status == 'success') {
            loading.close();
            this.$message({
              type: 'success',
              showClose: true,
              duration: 3000,
              dangerouslyUseHTMLString: true,
              message: '<span style="color:black;">下架商品成功</span>'
            });
            this.queryParam.currentPage = 1;
            that.select_flag_equipment();
          } else {
            console.info("下架商品失败");
            loading.close();
            if (res.data.msg === '请登录认证') {
              this.$router.push({path: '/login'});
            }
          }
        }, error => {
          loading.close();
          // this.companyDialogFormVisible =false;//保存失败关闭dialog
          this.$alert(JSON.stringify(error.message), '下架商品出错!', {
            confirmButtonText: '确定',
            callback: action => {

            }
          });
        }).catch((res) => {
          console.log("异步调用失败返回:" + JSON.stringify(res));
          loading.close();
          // this.companyDialogFormVisible =false;//保存失败关闭dialog
          this.$alert(JSON.stringify(res.message), '下架商品出错', {
            confirmButtonText: '确定',
            callback: action => {

            }
          });
        });
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '已取消保存'
        });
      });
    },
    //查询首页上架商品
    select_flag_equipment() {
      let that = this;
      that.tableData = [];
      let _jsondata_ = {
        flag: '3',
        mgs_device_uuid: that.di_uuid,
        perPageSize: that.queryParam.pageSize,
        currentPage: that.queryParam.currentPage,
      };
      const loading = this.$loading({
        lock: true,
        text: '提交中...',
        target: '.main',
        spinner: 'el-icon-loading',
        background: 'rgba(0, 0, 0, 0)'
      });
      var a1 = this.customGlobal.postHttpClient(
        this.customGlobal.serverUrl.postCUDMobileBankGoodsShelvesInfo,
        //this.customGlobal.serverUrl.baseURI,
        this.customGlobal.serverUrl.orgBaseURI,
        {"Content-Type": "application/x-www-form-urlencoded"},
        this.qs.stringify({jsondata: JSON.stringify(_jsondata_)})
      );
      a1.then((res) => {
        if (res.data.status === 'success') {
          loading.close();
          that.tableData = res.data.rows;
          that.queryParam.totalRow = res.data.total;
        } else {
          console.info("商品查询失败");
          loading.close();
          if (res.data.msg === '请登录认证') {
            this.$router.push({path: '/login'});
          }
        }
      }, error => {
        loading.close();
        // this.companyDialogFormVisible =false;//保存失败关闭dialog
        this.$alert(JSON.stringify(error.message), '商品查询失败!', {
          confirmButtonText: '确定',
          callback: action => {

          }
        });
      }).catch((res) => {
        console.log("异步调用失败返回:" + JSON.stringify(res));
        loading.close();
        // this.companyDialogFormVisible =false;//保存失败关闭dialog
        this.$alert(JSON.stringify(res.message), '商品查询失败', {
          confirmButtonText: '确定',
          callback: action => {

          }
        });
      });
    },
  }
}
</script>

<style scoped>
.account-plat-formitem /deep/ .el-form-item {
  margin-bottom: 25px !important;
}

.account-plat-card1 /deep/ .el-card__header {
  /*background:#f5f5f5 !important;*/
  background: #f2f3f8 !important;
  /*height: 10px!important;*/
  padding: 10px !important;
}

.account-plat-card2 /deep/ .el-card__body {
  padding-top: 5px !important;
  padding-bottom: 5px !important;
  padding-left: 20px !important;
  padding-right: 20px !important;
}

.account-plat-fixed-right /deep/ .el-table__fixed-right {
  height: 100% !important;
}

.account-plat-fixed /deep/ .el-table__fixed {
  height: 100% !important;
}

.box:after {
  content: ".";
  clear: both;
  height: 0;
  overflow: hidden;
  display: block;
  visibility: hidden;
}

.account-plat-dialog /deep/ .el-dialog__body {
  padding: 0px 20px !important;
  color: #606266 !important;
  font-size: 14px !important;
  word-break: break-all !important;
  margin-left: 0px !important;
}

.Tips /deep/ .el-form-item__error {
  left: 15%;
}
</style>



